import React from "react";
import { CLIENT_OWNER } from "../../configuration";
import LogoPG from "./../../images/pg/logo1.png";
import LogoSafra from "./../../images/safra/logo1.png";

let logo;

switch (CLIENT_OWNER) {
  case "safra":
    logo = LogoSafra;
    break;

  default:
    logo = LogoPG;
    break;
}

const Logo = () => {
  return <img src={logo} style={{ width: "12rem" }} alt="logo" />;
};

export default Logo;
