import {
  Dialog,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Slide,
  Button,
  DialogActions,
} from "@material-ui/core";
import React, { useState } from "react";
import AlertContext from "../../context/AlertContext";

const defaultData = {
  open: false,
  title: "",
  message: "",
  buttonText: "",
  onClick: () => {},
};

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const AlertProvider = ({ children }) => {
  const [data, setData] = useState(defaultData);

  const close = () => {
    setData((d) => ({ ...d, open: false }));
    setTimeout(() => {
      setData(defaultData);
    }, 500);
  };

  return (
    <AlertContext.Provider value={{ data, setData, close }}>
      {children}

      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={data.open}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => setData((data) => ({ ...data, open: false }))}
        aria-labelledby={"alert-dialog-slide-title"}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{data.title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            {data.message}
          </DialogContentText>
        </DialogContent>
        <DialogActions
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          {data.buttonText && (
            <Button variant="contained" color="primary" onClick={data.onClick}>
              {data.buttonText}
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </AlertContext.Provider>
  );
};

export default AlertProvider;
