import { makeStyles } from "@material-ui/core";
import { Button, Step, StepLabel, Stepper } from "@material-ui/core";
import React from "react";

const useStyle = makeStyles((theme) => {
  return {
    steeperContainer: {
      width: "100%",
      backgroundColor: theme.palette.background.default,
    },
    steeper: {
      backgroundColor: theme.palette.background.default, //#808080
    },
    stepLabel: { color: theme.palette.primary.contrastText },
  };
});

const StepperProvaDeVida = ({ activeStep }) => {
  const classes = useStyle();
  return (
    <div className={classes.steeperContainer}>
      <Stepper
        activeStep={activeStep}
        alternativeLabel
        className={classes.steeper}
      >
        <Step>
          <StepLabel className={classes.stepLabel}>Selfie</StepLabel>
        </Step>
        <Step>
          <StepLabel className={classes.stepLabel}>Prova De Vida</StepLabel>
        </Step>
        <Step>
          <StepLabel className={classes.stepLabel}>Conclusão</StepLabel>
        </Step>
      </Stepper>
    </div>
  );
};

export default StepperProvaDeVida;
