import React, { useState } from "react";
import StreamContext from "../../context/StreamContext";

const StreamProvider = ({ children }) => {
  const [stream, setStream] = useState(null);

  return (
    <StreamContext.Provider
      value={{
        stream,
        setStream,
      }}
    >
      {children}
    </StreamContext.Provider>
  );
};

export default StreamProvider;
