import React from "react";

const Elipse = ({ className, width, height }) => {
  return (
    <div
      className={className}
      style={{
        width: width / 2 - 30,
        height: height - 30,
        top: 15, // height / 2 - height / 2 / 2,
        left: width / 2 - width / 2 / 2 + 15,
      }}
    />
  );
};

export default Elipse;
