import { makeStyles } from "@material-ui/core";
import React, { useEffect } from "react";
import useRestrictOrientation from "../../hooks/useRestrictOrientation";
import useViewPort from "../../hooks/useViewPort";
import { alignCenter, flex, justifyCenter } from "../../jss";
import Video from "../Video";
import Elipse from "./Elipse";
import { chalangeTypes } from "../../util/constants";

const useStyle = makeStyles((theme) => {
  return {
    root: {
      width: "100%",
      ...flex,
      ...justifyCenter,
      ...alignCenter,
    },
    container: {
      position: "relative",
      overflow: "hidden",
    },
    elipse: {
      margin: 0,
      padding: 0,
      position: "absolute",
      zIndex: 100,
      borderColor: theme.palette.primary.main,
      borderWidth: "2px",
      borderStyle: "solid",
      borderRadius: "50%",
      boxShadow: "0 0 0 9999px rgba(0, 0, 0, 0.8)",
      // transform: "scale(1,2)",
    },
    video: {
      margin: 0,
      padding: 0,
      position: "absolute",
      top: 0,
      bottom: 0,
      zIndex: 90,
    },
  };
});
const VideoContainer = ({ chalangeType }) => {
  const classes = useStyle();
  const { width, height, orientation } = useViewPort();
  useRestrictOrientation(orientation);

  return (
    <div className={classes.root}>
      <div className={classes.container} style={{ width, height }}>
        {/* Render elipse */}
        {chalangeType === chalangeTypes.ELIPSE && (
          <Elipse className={classes.elipse} width={width} height={height} />
        )}

        {/* Render square */}
        {chalangeType === chalangeTypes.NOISE_SQUARE && <strong>square</strong>}

        <Video className={classes.video} style={{ width, height }} />
      </div>
    </div>
  );
};

export default VideoContainer;
