import { Button, makeStyles } from "@material-ui/core";
import React, { useContext, useEffect } from "react";
import Container70 from "../../components/Container70";
import StepperProvaDeVida from "../../components/StepperProvaDeVida";
import VideoContainer from "../../components/VideoContainer";
import StreamContext from "../../context/StreamContext";
import { flex, flexColumn } from "../../jss";
import { chalangeTypes } from "../../util/constants";

const useStyle = makeStyles((theme) => {
  return {
    root: {
      width: "100%",
      backgroundColor: theme.palette.background.default,
      ...flex,
      ...flexColumn,
    },
  };
});

const Self = (props) => {
  const classes = useStyle();

  const { stream } = useContext(StreamContext);
  useEffect(() => {
    if (!stream) props.history.replace("/");
  }, []);
  return (
    <Container70
      content={
        <div className={classes.root}>
          <StepperProvaDeVida activeStep={0} />
          <VideoContainer chalangeType={chalangeTypes.ELIPSE} />
        </div>
      }
      footer={
        <Button variant="contained" color="primary">
          Capturar
        </Button>
      }
    ></Container70>
  );
};

export default Self;
