import { useEffect } from "react";
import useAlert from "./useAlert";

export default (orientation) => {
  const alert = useAlert();

  useEffect(() => {
    if (orientation === "landscape") {
      alert.setData({
        open: true,
        title: "Orientação Invalida!",
        message: "Para prosseguir o celular precisa estar em pé",
        buttonText: null,
      });
    } else {
      alert.close();
    }
  }, [orientation]);
};
