import { Button } from "@material-ui/core";
import React, { useContext, useState } from "react";
import Container70 from "../../components/Container70";
import Wellcome from "../../components/Wellcome";
import { makeStyles } from "@material-ui/core";
import { getStream } from "../../util/video";
import StreamContext from "../../context/StreamContext";
import AlertContext from "../../context/AlertContext";

const useStyle = makeStyles((theme) => {
  return {
    root: {},
    botao: {
      width: 150,
    },
  };
});
const Home = (props) => {
  const classes = useStyle();
  const { stream, setStream } = useContext(StreamContext);
  const { setData, close } = useContext(AlertContext);
  const [tries, setTries] = useState(0);
  return (
    <Container70
      content={<Wellcome></Wellcome>}
      footer={
        <>
          <Button
            variant="contained"
            color="primary"
            className={classes.botao}
            onClick={() => {
              getStream()
                .then(setStream)
                .then(() => {
                  props.history.push("/self");
                })
                .catch((e) => {
                  if (tries >= 2) {
                    setData({
                      open: true,
                      title: "Alguma coisa deu errado!",
                      message:
                        "Tentamos por três vezes e sua permissão para ligar a camêra não foi efetivada, vamos encerrar esse processo e entraremos em contato no seu WhatsApp e faremos juntos! Obrigado!",
                      buttonText: "Ok, Encerrar",
                      onClick: () => {
                        close();
                      },
                    });
                  } else {
                    setTries(tries + 1);

                    setData({
                      open: true,
                      title: "Alguma coisa deu errado!",
                      message:
                        "Sua permissão não foi efetivada, se isso não for feito não conseguiremos tirar sua selfie",
                      buttonText: "Tentar Novamente",
                      onClick: () => {
                        close();
                      },
                    });
                  }
                });
            }}
          >
            Começar
          </Button>
        </>
      }
    ></Container70>
  );
};

export default Home;
