import React from "react";
import permissaoImg from "./../../images/permissao.png";

import Logo from "./Logo";

import { makeStyles, Typography } from "@material-ui/core";
import Video from "../Video";

const useStyle = makeStyles((theme) => {
  return {
    root: {
      width: "100%",
      display: "flex",
      justifyContent: "space-around",
      flexDirection: "column",
      backgroundColor: "#ffffff",
      paddingRight: "2.5rem",
      paddingLeft: "2.5rem",
    },
    logoWrapper: {
      width: "100%",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    content: {
      color: theme.palette.secondary.contrastText,
      display: "flex",
      justifyContent: "space-around",
      flexDirection: "column",
    },
    parag: {
      lineHeight: 1.7,
      marginBottom: 10,
    },
    permissaoWrapper: {
      width: "100%",
      display: "flex",
      justifyContent: "center",
    },
    permissao: { maxWidth: 345 },
  };
});

const Wellcome = () => {
  const classes = useStyle();

  return (
    <div className={classes.root}>
      <div className={classes.logoWrapper}>
        <Logo />
      </div>
      <div className={classes.content}>
        <Typography className={classes.parag} variant="body1" color="initial">
          Seja bem vindo!
        </Typography>
        <Typography className={classes.parag} variant="body1" color="initial">
          1. Para inciarmos você presisa autorizar o acesso a sua camera e sua
          localização
        </Typography>

        <Typography className={classes.parag} variant="body1" color="initial">
          2. Clique em <strong>começar</strong> e solicitaremos a autorização, é
          só clicar e <strong>permitir</strong>
        </Typography>
      </div>
      <div className={classes.permissaoWrapper}>
        <img className={classes.permissao} src={permissaoImg} alt="" />
      </div>
    </div>
  );
};

export default Wellcome;
