import _, { toString, get, max } from "lodash";
import IMask from "imask";

moment.locale("pt-BR");

// #region REGEX
export const REGEX_CPF = /(\d{3})(\d{3})(\d{3})(\d{2})/;
export const REGEX_REPLACE_CPF = "$1.$2.$3-$4";
export const REGEX_CNPJ = /(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/;
export const REGEX_REPLACE_CNPJ = "$1.$2.$3/$4-$5";
export const REGEX_TO_VALIDATE_CPF = /\d{3}\.\d{3}\.\d{3}-\d{2}|\d{11}/;
export const REGEX_NOME_COMPLETO = /[A-Z][a-z]* [A-Z][a-z]*/;
// #endregion REGEX

// #region Utilitários HTML

/**
 * Contatena string para consolidar classes utilizadas no componentes
 * @param {Array<string>} classesCollection Lista de classes
 */
export const combinarClasses = (classesCollection) =>
  classesCollection.join(" ");

export const cls = (...parans) => combinarClasses([...parans]);
// #endregion Utilitários HTML

// #region Utilitários ARRAY

/**
 * Remover item de um array imutável
 * @param {Array<any>} lista Lista
 * @param {number} index Indice
 */
export const removerItemLista = (lista = [], index) => {
  return [...lista.slice(0, index), ...lista.slice(index + 1)];
};

// Adicionar item a um array imutável
export const adicionarItemLista = (lista = [], item) => {
  return [...lista, item];
};

export const adicionarLista = (novaLista = []) => {
  return [...novaLista];
};

// Altera um item de um array imutável
export const alterarItemLista = (lista = [], index, item) => {
  return [...lista.slice(0, index), item, ...lista.slice(index + 1)];
};

// Adiciona ou Altera um item de um array imutável
export const adicionarOuAlterarItemLista = (lista = [], item) => {
  const expressao = (x) =>
    !_.isNil(item) && !_.isNil(x.uid) && x.uid === item.uid;
  const index = _.findIndex(lista, expressao);
  if (index === -1) {
    return adicionarItemLista(lista, item);
  }
  return alterarItemLista(lista, index, item);
};

// Filtra a primeira ocorrência de uma expressâo em um array com o índice
export const procurarItemIndice = (lista, interacao) => {
  return [_.find(lista, interacao), _.findIndex(lista, interacao)];
};

/*
  Filtra a primeira ocorrência de uma expressâo em um array
  e retorna valor padrao quando nao encontra
*/
export const procurarItemPadrao = (lista, interacao, valorPadrao) => {
  const valorEncontrado = _.find(lista, interacao);
  return _.isNil(valorEncontrado) ? valorPadrao : valorEncontrado;
};

// MApeia array para um elemento <Selecao />
export const mapearListaSelecao = (lista, campoValor, campoTexto) =>
  _.map(lista, (item) => ({
    text: item[campoTexto],
    value: item[campoValor],
  }));

// MApeia array para um elemento react-select
export const mapearListaReactSelect = (
  lista,
  campoValor,
  campoTexto = "nome"
) =>
  _.map(lista, (item) => ({
    label: item[campoTexto],
    value: item[campoValor],
  }));

export const primeiro = (lista, retornoPadrao) =>
  _.head(lista) || retornoPadrao;

export const ultimo = (lista, retornoPadrao) => _.last(lista) || retornoPadrao;

