export const removerNulosOuIndefinidos = (objeto) => {
  const objetoNovo = {};

  Object.keys(objeto).forEach((chave) => {
    if (objeto[chave] && typeof objeto[chave] === "object") {
      objetoNovo[chave] = removerNulosOuIndefinidos(objeto[chave]);
    } else if (estaNuloOuIndefinido(objeto[chave])) {
      objetoNovo[chave] = objeto[chave];
    }
  });

  return objetoNovo;
};

export const safeString = (value) => {
  return _.isNil(value) ? "" : value;
};

export const convertFileToBase64 = async (file) => {
  return new Promise((resolve, reject) => {
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      resolve(reader.result);
    };
    reader.onerror = (erro) => {
      reject(erro);
    };
  });
};

/**
 *
 * @returns {Promise}
 */
export const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export const getExtensionFromBase64 = (base64) =>
  base64.substring("data:image/".length, base64.indexOf(";base64"));

export const formataBoolToPort = (bool) => {
  return bool ? "Sim" : "Não";
};

export const phoneYupValidation = () =>
  yup.string().test("Telefone", "Preencha esse campo corretamente", (value) =>
    toString(value)
      .replace(/\D/g, "")
      .match(/\d{10,11}/)
  );

export const getColWidth = (list, col, title = "") => {
  const getColLength = (row) => get(row, col, "")?.length || 1;

  let chartCount = max([...list.map(getColLength), title.length]);

  const LETTER_WIDTH = 12.00555555555556;

  return chartCount * LETTER_WIDTH;
};

export const setCaretPosition = (elem, caretPos) => {
  if (elem != null) {
    if (elem.createTextRange) {
      var range = elem.createTextRange();
      range.move("character", caretPos);
      range.select();
    } else if (elem.selectionStart) {
      elem.focus();
      elem.setSelectionRange(caretPos, caretPos);
    } else elem.focus();
  }
};

export const formatToCC = (value) => safeString(value).replace(/(\d$)/, "-$1");

export const noInternalError = (status) => {
  if (status === 408 || status === 500) {
    return false;
  }
  return true;
};

// cria array de objetos com range de valores decimais
export const criaRangeComDecimais = (range) => {
  const { taxaMin, taxaMax } = range;
  let taxasRange = [];
  for (let valor = taxaMin; valor < taxaMax; valor += 0.01) {
    taxasRange.push({
      descricao: valor.toLocaleString("pt-BR", {
        minimumFractionDigits: 2,
      }),
      valor: Math.round(valor * 100) / 100,
    });
  }
  return taxasRange;
};
